import React from 'react';
import s from 'skills/Skills.module.scss'
import {Skill} from "./Skill/Skill";
import blockBackground from 'assets/images/fon-2.jpg'
import { motion } from 'framer-motion';
import {textBgAnimation} from "assets/animation/animation";

export const Skills = () => {
    return (
        <div
            className={s.skillsBlock}
            style = {{backgroundImage: `url('${blockBackground}')`}}
            id={'anchorSkills'}>
            <div className={"container"}>
                <motion.h2
                    initial={"hidden"}
                    whileInView={"visible"}
                    viewport={{once: true, amount: 0.9}}>
                    <span className="titleBg">
                        <motion.span className="titleBgIn" variants={textBgAnimation}></motion.span>
                    </span>
                </motion.h2>
                <div className={s.skills}>
                    <Skill name={"JS"} icon={'js'} desc={"    js.    .        .    ."}/>
                    <Skill name={"React"} icon={'react-logo'} desc={"React + Redux -        .   pet   React.    redux,      thunk  axios,  unit ,  storybook,  material ui.       React."}/>
                    <Skill name={"HTML"} icon={'html'} desc={" 6    ,    .   WordPress  October CMS.   HTML,        js  React.  ,      ."}/>
                    <Skill name={"CSS"} icon={'css3'} desc={"    css .  ,  ,  .     pet   scss.           css."}/>
                </div>
            </div>
        </div>
    );
};

